body{
    color:black ;
}
.Importance-content h2{
    padding-left: 400px;
    padding-top: 80px;

}
.Importance-content {
    background-color:black;

}
.heading span{
    color: red;
}
.heading {
    margin-left: 60px;
    margin-top: -1px;
    color: white;
    font-size: 4.5rem;
    text-align: center;
}

.Importance-content h2 {
    margin-left: 60px;
    color: white;
    text-align: left;
    line-height: 1.2;
}

.Importance-content h3 {
    margin-left: 60px;
    color: white;
    font-size: 20px;
}

.Importance-content p {
    color: white;
    margin-left: 60px;
    font-size: 20px;
    margin: 2rem 0 3rem;
}
.btn {
    display: inline-block;
    padding: .5rem 1rem;
    margin-left: 60px;
    margin-top: 10px;
    background: red;
    border-radius: .6rem;
    box-shadow: 0 .2rem .5rem var(--shadow-color);
    font-size: 1.3rem;
    color:white;
    letter-spacing: .1rem;
    font-weight: 600;
    border: .2rem solid transparent;
    transition: .5s ease;
}

.btn:hover {
    background: transparent;
    color: red;
    border-color:red;
}
@media (max-width: 700px) {
    .Importance-content {
        width: 95.5%; /* Change to 100% for better responsiveness */
        padding: 10px; /* Add padding for smaller screens */
    }

    .Importance-content h2 {
        margin-left: 0; /* Remove left margin for centering */
        text-align: center; /* Center text */
        padding-left: 0; /* Remove padding */
    }

    .heading {
        margin-left: 0; /* Center heading */
        font-size: 3rem; /* Smaller font size for mobile */
    }

    .Importance-content h3,
    .Importance-content p {
        margin-left: 0; /* Remove left margin */
        text-align: center; /* Center text */
        font-size: 1.3rem; /* Adjust font size */
    }

    .btn {
        margin-left: 20px; /* Center button */
        width: 85%; /* Full width button */
        text-align: center; /* Center text */
        margin-top: 20px; /* Adjust margin */
    }
}