@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');


.Language {
    height: 700px;
    background-color: black;
}
.Language .Language-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 2.5rem;
}
.Language h2{
    padding-top: 87px;
}

.Language-container .Language-box {
    position: relative;
    width: 80%;
    height: 80%;
    margin-left: 10%;
    display: flex;
    border-radius: 2rem;
    box-shadow: 0 0 1rem rgba(255, 0, 0, 0.986);
    overflow: hidden;
}

.Language-box img {
    width: 100%;
    transition: .5s ease;
}

.Language-box:hover img {
    transform: scale(1.1);
}

.Language-box .Language-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, .1), var(--main-color));
    color:black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    opacity: 0;
    transition: .5s ease;
}

.Language-box:hover .Language-layer {
    opacity: 2;
}

.Language-layer h4 {
    align-items: center;
    margin-top: 35%;
    margin-right: 40%;
    font-size: 3rem;
}
.Language-layer .java{
    align-self: center;
    text-align: center;
    
    font-size: 2.5rem;
}

.Language-layer p {
    font-size: 1.6rem;
    margin: .3rem 0 1rem;
}

.Language-layer a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background: white;
    background-color: rgba(255, 0, 0, 0.589);
    border-radius: 50%;
    margin-right: 40%;
}

.Language-layer a i {
    font-size: 2rem;
    color: #333;
}
@media (max-width: 700px) {
    .Language {
        height: auto; 
    }
    
    .Language .Language-container {
        grid-template-columns: 1fr; 
        gap: 1rem; 
    }
    
    .Language h2 {
        font-size: 2.5rem;
        text-align: center; 
        margin-left: 0; 
        padding-top: 90px; 
    }
    
    .Language-layer h4 {
        margin-left: 35px;
        font-size: 4rem; 
    }

    .Language-layer p {
        font-size: 1.2rem; 
    }

    .Language-layer a {
        margin-top: -50px;
        margin-left: 20px;
        width: 6rem; 
        height: 6rem; 
    }

    .Language-layer a i {
        font-size: 1.9rem; 
    }
}