.home-content {
    margin-top: -10px;
    height: 650px;
    background-color: black;
}

.home-content h3 {
    color: white;
    font-size: 2.5rem;
    font-weight: 700;
    line-height: .3;
    margin-left: 70px;
    margin-top: 130px;
}
.home-content h1 span{
    color: red
}
.home-content h1 {
    color: white;
    font-size: 3.5rem;
    font-weight: 700;
    margin-left: 60px;
    margin-top: -15px;
}

.home-content p {
    color: white;
    font-size: 1.5rem;
    width: 600px;
    margin-left: 60px;
}
.home-img img {
    margin-top: 120px;
    display: flex;
    position: absolute;
    right: 80px;
    pointer-events: none;
    max-width: 480px;
    max-height: 900px;
}
@media (max-width: 700px) {
    .home-content {
        margin-top: 10px;
        height: auto; /* Allow height to adjust */
        padding: 10px; /* Adjust padding for smaller screens */
    }

    .home-content h3 {
        margin-top:130px ;
        margin-left: 15px;
        font-size: 2.9rem; /* Smaller font size */
    }

    .home-content h1 {
        margin-top: 1px;
        margin-left: 10px;
        font-size: 2.7rem; /* Smaller font size */
    }

    .home-content p {
        width: auto; /* Auto width for better responsiveness */
        text-align: center; /* Center text */
        font-size: 1.5rem; /* Smaller font size */
        margin: 10px 0; /* Adjust margin */
    }

    .home-img {
        width: 100%; /* Full width on mobile */
        height: auto; /* Maintain aspect ratio */
        margin-top: 20px; /* Space above */
        display: flex; /* Flexbox for layout */
        justify-content: center; /* Center the image */
    }

    .home-img img {
        margin-right: -70px;
        margin-top: 70px;
        max-width: 40%; /* Responsive image size */
        height: auto; /* Maintain aspect ratio */
    }
}
