.navbar {
    position: fixed;
    top: 0;        
    left: 0;
    right: 0;
    z-index: 1000; 
    background-color: #000000; 
    transition: background-color 0.3s; 
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.navbar .logo {
    color: #fa0000;
    font-weight: bold;
    font-size: 1.5rem;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 1.9rem;
    font-weight: bold;
    font-size: 1.1rem;
    margin-right: 30px;
}

.nav-links a {
    color: #fff;
    text-decoration: none;
    position: relative; 
}

.nav-links a:hover {
    color: #fff; 
}

.nav-links a::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px; 
    background-color: #fc0000; 
    left: 0;
    bottom: -5px; 
    transform: scaleX(0); 
    transition: 0.3s ease; 
}

.nav-links a:hover::after {
    transform: scaleX(1); 
}

.menu-toggle {
    display: none; 
    cursor: pointer;
    color: #ffffff;
}

@media (max-width: 700px) {
    .nav-links {
        margin-right: 1px;
        font-size: 1.5rem;
        display: none; 
        flex-direction: column;
        position: absolute;
        background-color: #000000;
        top: 60px; /* Adjust to avoid overlap with the navbar */
        right: 0;
        width: 50%;
        padding: 1rem;
        height: auto; 
        max-height: 500px; 
        overflow-y: auto; 
        gap: 1.3rem;
    }

    .nav-links.open {
        display: flex; 
    }

    .menu-toggle {
        font-size: 2rem;  
        display: block; 
        margin-right: 20px; /* Adjust margin */
    }

    .navbar {
        padding: 1.5rem; /* Ensure consistent padding */
    }
}