@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');

@import url("https://cdn.jsdelivr.net/npm/swiper@9/swiper-bundle.min.css");
@import url("https://unpkg.com/boxicons@2.1.4/css/boxicons.min.css");

.contact {
    background-color: black;
    height: 620px;
}
.contact .social-media a:hover {
    background:red;
    color:white;
}
.contact h2 {
    padding-top: 70px;
    margin-bottom: 7rem;
}

.contact .social-media a {
    margin-left: 13%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 6rem;
    height: 6rem;
    background: transparent;
    border: .2rem solid red;
    border-radius: 50%;
    box-shadow: 0 .2rem .5rem red;
    font-size: 2rem;
    color:red;
    transition: .5s ease;
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: .1rem;
    background: red;
}

.footer-text p {
    display: flex;
    width: auto;
    margin-left: 85px;
    font-size: 1.3rem;
    color:white;
}

.footer-iconTop a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: .1rem;
    background: white;
    border-radius: .8rem;
    border: .2rem solid black;
    outline: .2rem solid transparent;
    transition: .5s ease;
    margin-right: 90px;
    margin-top: -5px;

    
}

.footer-iconTop a:hover {
    outline-color: white;
}

.footer-iconTop a i {
    font-size: 2.5rem;
    color: #333;
}
@media (max-width: 700px) {
    .contact {
        height: auto; /* Allow height to adjust based on content */
        padding: 90px; /* Add padding for mobile */
    }

    .contact h2 {
        margin-top: -20px;
        font-size: 2.5rem; /* Reduce font size for headings */
        text-align: center; /* Center the heading */
    }
    .contact .social-media {
        margin-left: -50px; /* Remove negative margin */
        display: flex; /* Ensure items are aligned properly */
        justify-content: center; /* Center the social media icons */
        gap: .1rem; /* Space between icons */
    }
    
    .contact .social-media a {
        width: 10rem; /* Adjust button size */
        height: 5rem; /* Adjust button size */
        font-size: 1.9rem; /* Icon size */
        display: inline-flex; /* Align icon and text */
        justify-content: center; /* Center content */
        align-items: center; /* Center content */
        border: .2rem solid red; /* Add border if needed */
        border-radius: 50%; /* Keep rounded */
        background-color: transparent; /* Background color */
        transition: background 0.3s ease; /* Smooth transition */
    }
    
    .contact .social-media a:hover {
        background: red; /* Change background on hover */
        color: white; /* Change icon color on hover */
    }

    .footer {
        flex-direction: column; /* Stack footer items */
        align-items: center; /* Center footer content */
    }
    .footer-iconTop{
        margin-left: 90px;
    }
    .footer-text p {
        margin-left: 1px;
        font-size: 1rem; /* Reduce footer text size */
    }
}